"use client"

import { Button } from "@/components/ui/button"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Bell, Plus, Send, Menu, Wallet, Server as ServerIcon } from "lucide-react"
import Link from "next/link"
import Image from "next/image"
import { signOut } from "@/lib/actions/auth"
import { 
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { Badge } from "@/components/ui/badge"
import { useState, useEffect } from "react"
import { useLanguage } from "@/contexts/language-context"
import { Sheet, SheetContent, SheetTrigger, SheetHeader as SheetHdr, SheetTitle } from "@/components/ui/sheet"
import { Tooltip, TooltipContent, TooltipTrigger } from "@/components/ui/tooltip"

interface User {
  id: string
  email: string
  name?: string
  role: string
}

interface Profile {
  fullName?: string
  balance: number
}

export function DashboardHeader() {
  const [user, setUser] = useState<User | null>(null)
  const [profile, setProfile] = useState<Profile | null>(null)
  const [orders, setOrders] = useState<any[]>([])
  const { t, language, setLanguage } = useLanguage()

  useEffect(() => {
    const abortController = new AbortController()
    const loadUserData = async () => {
      try {
        const userRes = await fetch("/api/user", { cache: "no-store", signal: abortController.signal })
        if (!userRes.ok) return
        const u = await userRes.json()
        setUser({ id: u.id, email: u.email, role: u.role, name: u.name })

        const [profileRes, ordersRes] = await Promise.all([
          fetch("/api/profile", { cache: "no-store", signal: abortController.signal }),
          fetch("/api/orders", { cache: "no-store", signal: abortController.signal }),
        ])

        if (profileRes.ok) {
          const p = await profileRes.json()
          setProfile({ fullName: p.fullName, balance: p.balance })
        }
        if (ordersRes.ok) {
          const o = await ordersRes.json()
          setOrders(Array.isArray(o) ? o : [])
        }
      } catch (error) {
        if ((error as any)?.name !== "AbortError") {
          console.error("[v0] Error loading user data:", error)
        }
      }
    }
    loadUserData()
    return () => abortController.abort()
  }, [])

  if (!user) return null

  const initials = profile?.fullName
    ? profile.fullName
        .split(" ")
        .map((n) => n[0])
        .join("")
        .toUpperCase()
        .slice(0, 2)
    : user.email?.substring(0, 2).toUpperCase() || "U"

  return (
    <header className="border-b border-border/50 backdrop-blur-sm bg-background/80 sticky top-0 z-50">
      <div className="container mx-auto px-3 sm:px-4 h-14 sm:h-16 flex items-center justify-between gap-2">
        <Link href="/dashboard" className="flex items-center gap-2 min-w-0">
          <div className="flex ">
            <Image
              src="/logovds.jpg"
              alt="–õ–æ–≥–æ—Ç–∏–ø VDS_HUB"
              width={102}
              height={102}
              className="object-cover w-8 h-8"
              priority
            />
          </div>
          <h1 className="text-lg sm:text-xl font-bold text-foreground truncate">VDS_HUB</h1>
        </Link>

        <div className="flex items-center gap-2 sm:gap-3 flex-shrink-0">
          {profile && (
            <div className="hidden md:flex items-center gap-2 text-sm">
             <Button variant="outline" size="sm" 
              onClick={() => setLanguage(language === "ru" ? "en" : "ru")}>
               {language === "ru" ? "EN" : "RU"}
              </Button>
              <Link href="/dashboard/billing" className="px-3 py-1 rounded-full bg-gradient-to-r from-primary/15 to-accent/15 border border-primary/30 text-foreground hover:from-primary/20 hover:to-accent/20 transition-colors flex items-center gap-2">
                <Wallet className="w-4 h-4 text-primary" aria-hidden="true" />
                
                <span className="text-muted-foreground">–ë–∞–ª–∞–Ω—Å</span>
                <span className="font-semibold">${(profile.balance ?? 0).toFixed(2)}</span>
              </Link>
              <Link href="/dashboard/servers" className="px-3 py-1 rounded-full bg-secondary/20 border border-secondary/30 text-foreground hover:bg-secondary/30 transition-colors flex items-center gap-2">
                <ServerIcon className="w-4 h-4" aria-hidden="true" />
                <span>VPS</span>
                <span className="font-semibold">{orders?.length || 0}</span>
              </Link>
            </div>
          )}
          <Sheet>
            <SheetTrigger asChild>
              <Button variant="ghost" size="icon" className="lg:hidden">
                <Menu className="w-5 h-5" />
              </Button>
            </SheetTrigger>
            <SheetContent side="left" className="p-0 w-80">
              <SheetHdr>
                <SheetTitle>–ú–µ–Ω—é</SheetTitle>
              </SheetHdr>
              <nav className="p-4 space-y-2">
                <Link href="/dashboard" className="block px-4 py-3 rounded-lg hover:bg-secondary">
                   {t("dashboard.title")}
                </Link>
                <Link href="/dashboard/servers" className="block px-4 py-3 rounded-lg hover:bg-secondary">
                   {t("servers.myServers")}
                </Link>
                <Link href="/dashboard/services" className="block px-4 py-3 rounded-lg hover:bg-secondary">
                   {t("sidebar.myServices")}
                </Link>
                <Link href="/dashboard/billing" className="block px-4 py-3 rounded-lg hover:bg-secondary">
                   {t("dashboard.billing")}
                </Link>
                <Link href="/dashboard/referrals" className="block px-4 py-3 rounded-lg hover:bg-secondary">
                  {t("dashboard.referrals")}
                </Link>
                <Link href="/dashboard/support" className="block px-4 py-3 rounded-lg hover:bg-secondary">
                   {t("sidebar.support")}
                </Link>
                {user.role?.toLowerCase() === "admin" && (
                  <Link href="/admin" className="block px-4 py-3 rounded-lg hover:bg-secondary">üõ°Ô∏è {t("dashboard.adminPanel")}</Link>
                )}
              </nav>
              <Button variant="outline" size="sm" 
              onClick={() => setLanguage(language === "ru" ? "en" : "ru")}>
              {language === "ru" ? "EN" : "RU"}
              </Button>
            </SheetContent>
          </Sheet>
          <Button
            variant="outline"
            size="sm"
            className="gap-2 px-3 hidden xs:flex rounded-full border-primary/30 bg-primary/5"
            onClick={() => setLanguage(language === "ru" ? "en" : "ru")}
          >
            <span className="text-sm">{language === "ru" ? "RU" : "EN"}</span>
          </Button>

          <Tooltip>
            <TooltipTrigger asChild>
              <Button variant="outline" size="sm" className="gap-2 bg-transparent hidden sm:flex" asChild>
                <Link href="/dashboard/billing">
                  <Plus className="w-4 h-4" />
                  <span className="hidden sm:inline">{t("dashboard.topUp")}</span>
                </Link>
              </Button>
            </TooltipTrigger>
            <TooltipContent sideOffset={6}>–ü–æ–ø–æ–ª–Ω–∏—Ç—å –±–∞–ª–∞–Ω—Å</TooltipContent>
          </Tooltip>

          <Tooltip>
            <TooltipTrigger asChild>
              <Button variant="ghost" size="icon" className="relative hidden sm:inline-flex" asChild>
                <Link href="https://t.me/VDS_HUB_Support" target="_blank">
                  <Send className="w-4 h-4" />
                </Link>
              </Button>
            </TooltipTrigger>
            <TooltipContent sideOffset={6}>Telegram</TooltipContent>
          </Tooltip>

          <Tooltip>
            <TooltipTrigger asChild>
              <Button variant="ghost" size="icon" className="relative hidden xs:inline-flex">
                <Bell className="w-4 h-4" />
                <span className="absolute top-1 right-1 w-2 h-2 bg-destructive rounded-full" />
              </Button>
            </TooltipTrigger>
            <TooltipContent sideOffset={6}>–£–≤–µ–¥–æ–º–ª–µ–Ω–∏—è</TooltipContent>
          </Tooltip>

          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button variant="ghost" size="icon" className="rounded-full">
                <Avatar className="w-8 h-8">
                  <AvatarFallback className="bg-primary text-primary-foreground">{initials}</AvatarFallback>
                </Avatar>
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent align="end" className="w-56">
              <DropdownMenuLabel>
                <div className="flex flex-col space-y-1">
                  <p className="text-sm font-medium">{profile?.fullName || user.email}</p>
                  <p className="text-xs text-muted-foreground">{user.email}</p>
                  <Badge variant="secondary" className="w-fit text-xs capitalize">
                    {user.role.toLowerCase()}
                  </Badge>
                </div>
              </DropdownMenuLabel>
              <DropdownMenuSeparator />
              <DropdownMenuItem asChild>
                <Link href="/dashboard/profile">{t("dashboard.profile")}</Link>
              </DropdownMenuItem>
              <DropdownMenuItem asChild>
                <Link href="/dashboard/billing">{t("dashboard.billing")}</Link>
              </DropdownMenuItem>
              <DropdownMenuItem asChild>
                <Link href="/dashboard/referrals">{t("dashboard.referrals")}</Link>
              </DropdownMenuItem>
              {user.role?.toLowerCase() === "admin" && (
                <DropdownMenuItem asChild>
                  <Link href="/admin">{t("dashboard.adminPanel")}</Link>
                </DropdownMenuItem>
              )}
              <DropdownMenuSeparator />
              <DropdownMenuItem
                className="text-destructive"
                onClick={async () => {
                  await signOut()
                }}
              >
                {t("dashboard.logout")}
              </DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>
        </div>
      </div>
    </header>
  )
}
